.tdimg {
    width: 120px;
}


tr {
    width: 100%;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid rgba(0, 0, 0, 0.355);
    align-items: center;
}

tr i {
    cursor: pointer;
    transition: 0.2s;
}

tr i:hover {
    scale: 1.1;
}


td, th {
padding:10px;
}

table 
{
    width: 60%;
    display: flex;
    flex-direction: column;
}

.product-name-c {
    font-weight: 700;
}

.nothing {
    margin: 0 auto;
    font-size: 18px;
    color: red;
}

.checkoutList {
    width: 300px;
    height: 400px;
    border-radius: 15px;
    box-shadow: 1px 1px 5px 2px rgba(0, 0, 0, 0.474);
    display: flex;
    flex-direction: column;
    gap: 25px;
    padding: 15px;
}

.tableCart {
    display: flex;
    width: 100%;
    justify-content: space-between;
    gap: 50px;
}

.checkout-btns {
    border: none;
    background: rgba(255, 81, 18, 0.815);
    color: #fff;
    padding: 10px;
    border-radius: 8px;
    transition: 0.4s;
}

.checkout-btns:hover {
    border: none;
    background: orangered;
    color: #fff;
    padding: 10px;
    border-radius: 8px;
}

.checkout-btns:active {
    scale: 0.9;
}

@media only screen  and (max-width:768px){
    .padding-default {
        padding:  20px 10px;
    }
    .tableCart {
        flex-direction: column;
        align-items: center;
    }

    

tr {
    max-width:768px;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid rgba(0, 0, 0, 0.355);
    align-items: center;
}

tr {
    scale:0.8;
}


td, th {
padding:10px;
}

table 
{
    width: 100%;
    display: flex;
    flex-direction: column;
}

.product-name-c {
    font-weight: 700;
}

}