
.ProductList {
    display: flex !important;
    justify-content: space-between;
    align-items: center;
    gap: 25px  !important;
    margin: 25px auto;
}

.product-img img{
    width: 220px;

}

.product-img img:hover{
    scale: 1.1;
}
.product-item {
    min-width:250px;
    width:250px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    min-height: 322px;
    cursor: pointer;
    /* box-shadow: 0px 2px 13px -3px rgba(0, 0, 0, 0.26); */
    border-radius:10px;
}

.product-item:hover {
    box-shadow: 0px 2px 13px -3px rgba(0, 0, 0, 0.26);
}



.product_name {
    font-size: 20px;
    font-weight: 700;
    color: var(--primary-color);
}

.product__info {
    padding: 10px 5px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.product-card_bottom { 
    display: flex;
    justify-content: space-between;
}

.buy__item {
    padding: 8px 10px;
    background: #000;
    border-radius: 50%;
    color: #fff;
}

.buy__item:hover {
    padding: 8px 10px;
    background: rgb(233, 129, 10);
    border-radius: 5px;
    color: #000;
    scale: 1.2;
}


@media only screen  and (max-width:768px){
 
.ProductList {
    display: flex !important;
    justify-content: center;
    align-items: center;
    gap: 25px  !important;
}   

.product-item {
    min-width:200px;
    flex: 1;
    max-width:165px;
    padding: 10px;
    display: flex;
    flex-direction: column;
}


.product_name {
    font-size: 18px;
    font-weight: 600;
    color: var(--primary-color);
}

.product-img img{
    max-width: 150px;
}
}